<template>
   <v-dialog v-model="show" max-width="450">
      <v-card>
        <v-card-title class="h1 deep-purple--text text--darken-4">Share your list </v-card-title>

        <v-card-text>
          Others can view your wish-list using the following link -
          <br>
          <br>
          {{link}}
        </v-card-text>

        <v-card-actions class="mt-0">
          <v-btn
            text
            color="deep-purple darken-1"
            @click="copyText"
          >
            Copy Link
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            @click.prevent="changeLink"
            text
            color="deep-purple darken-1"
          >
            New link
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
import UserService from '../services/user.service';

export default {
  name: 'ShareModal',
  props: ['value'],
  data() {
    return {
      link: `${location.origin.toString()}/shared/${this.$store.state.auth.user.user.shareablelink}`,
    }
  },
  computed: {
    show: {
      get () {
        return this.value
      },
      set (value) {
         this.$emit('input', value)
      }
    }
  },
  methods: {
    copyText() {
      navigator.clipboard.writeText(this.link);
    },
    async changeLink() {
      try {
        const res = await UserService.resetShareLink();
        const { link } = res;
        this.$store.commit('auth/changeShareLink', link);
        this.link = `${location.origin.toString()}/shared/${link}`;
      } catch (err) {
        throw new Error(err);
      }
    }
  }
}
</script>