<template>
    <v-app>
       <v-main class="app pt-0">
       <p class="mt-6 mb-10 deep-purple--text text--darken-3 text-center text-h4 text-sm-h3 font-weight-light">{{name}}'s wishlist</p>
        <v-card elevation="3" width="600" class="my-5 mx-auto" v-for="wish in wishlist" :key="wish._id" >
            <v-card-title>
                <span class="deep-purple--text text--darken-2">{{ wish.title }}</span>
            </v-card-title>
            <v-card-text>
                {{ wish.description }}
            </v-card-text> 
            <v-card-actions class="mt-0 pt-0">
                <v-btn
                    text
                    color="deep-purple accent-5"
                    :href="wish.link"
                    target="_blank"
                >
                    Buy / View
                </v-btn>
            </v-card-actions>
        </v-card>
       </v-main>
    </v-app>
</template>

<script>
import UserService from '@/services/user.service';

export default {
    data() {
        return {
            wishlist: null,
            name: ''
        }
    },
    async created() {
        try {
            const data = await UserService.getSharedWishlist(this.$route.params.id);
            const { name, wishes } = data.data;
            this.wishlist = wishes;
            this.name = name;
        }
        catch (err) {
            this.$router.push('/');
        }
    },
}
</script>

<style scoped>
    .app {
        background-color: lavender;
    }
</style>