<template>
  <v-app>
    <v-container fluid fill-height class="app">
    <v-row align="center" justify="center">
      <v-card width="400" class="mx-auto"> 
        <v-card-title class="deep-purple--text text--darken-2 text-h4 font-weight-bold pl-8 mt-3">Sign up</v-card-title>
        <v-card-text>
          <v-form v-model="isFormValid" ref="form">
          <v-text-field 
            v-model="user.name"
            class="pl-3 pr-5"
            label="Name" 
            :rules="nameRules"
            prepend-icon="mdi-account-circle"
          />
          <v-text-field 
            v-model="user.email"
            class="pl-3 pr-5"
            label="Email" 
            :rules="emailRules"
            type="email"
            prepend-icon="mdi-email"
          />
          <v-text-field 
            v-model="user.password"
            class="pl-3 pr-5"
            label="Password" 
            :rules="passwordRules"
            :type="showPassword? 'text' : 'password'" 
            prepend-icon="mdi-lock" :append-icon="showPassword? 'mdi-eye': 'mdi-eye-off'"
            @click:append="showPassword = !showPassword"
          />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn 
            :disabled="!isFormValid"
            @click.prevent="handleRegister"
            width="90%" 
            color="deep-purple white--text" 
            class="mx-auto ml-4 mr-4"
            > Create account
            </v-btn>
        </v-card-actions>   
        <v-card-text class="deep-purple--text text--lighten-1">
          <p class="text-center mb-1 formLink"><router-link to="/">Have an account? Log in.</router-link></p>
        </v-card-text>
      </v-card>
    </v-row>
    </v-container>
    <v-snackbar
      max-width="80%"
      v-model="submitted"
      :timeout="6000"
      :top="true"
      :color="successful ? 'success' : 'error'"
    >
     {{message}}
    </v-snackbar>
  </v-app>
</template>

<script>
import User from '@/models/user';

export default {
  data() {
    return {
      showPassword: false,
      user: new User('', '', ''),
      submitted: false,
      successful: false,
      isFormValid: false,
      message: '',
      nameRules: [
        v => ( v && !!v.trim()) || 'Name is required'
      ],
      emailRules: [
        v => (v && !!v.trim()) || 'Email is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
      ],
      passwordRules: [
        v => (v && !!v.trim()) || 'Password is required',
        v => (v && v.length >= 8) || 'Password must be atleast 8 characters long'
      ]
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    }
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push('/wishes');
    }
  },
  methods: {
    async handleRegister() {
      try {
        await this.$store.dispatch('auth/register', this.user);
        this.$refs.form.reset();
        this.message = 'Created account successfully!';
        this.submitted = true;
        this.successful = true;
      }
      catch (err) {
        this.message = 'An error occured. Could not create the account.';
        this.successful = false;
        this.submitted = true;
      }
    }
  }
}
</script>

<style scoped>
    .app {
       background-color:  #5E35B1;
    }
    a {
        text-decoration: none;
        color: inherit !important;
    }
    .formLink {
      cursor: pointer;
    }
    .formLink:hover {
      color: #401786 !important;
    }
</style>