<template>
  <v-app>
    <v-container fluid fill-height class="app">
    <v-row align="center" justify="center">
      <v-card  width="400" class="mx-auto"> 
        <v-card-title class="deep-purple--text text--darken-2 text-h4 font-weight-bold pl-8 mt-3">Sign in</v-card-title>
        <v-card-text>
          <v-form v-model="isFormValid">
          <v-text-field 
            v-model="user.email"
            class="pl-3 pr-5"
            label="Email" 
            :rules="emailRules"
            prepend-icon="mdi-email"
          />
          <v-text-field 
            v-model="user.password"
            class="pl-3 pr-5"
            label="Password" 
            :rules="passwordRules"
            :type="showPassword? 'text' : 'password'" 
            prepend-icon="mdi-lock" :append-icon="showPassword? 'mdi-eye': 'mdi-eye-off'"
            @click:append="showPassword = !showPassword"
          />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn 
            @click.prevent="handleLogin"
            :disabled="loading || !isFormValid"
            width="90%" 
            color="deep-purple white--text" 
            class="mx-auto ml-4 mr-4">
            Login
          </v-btn>
        </v-card-actions>   
        <v-card-text class="deep-purple--text text--lighten-1">
          <p class="text-center mb-1 formLink"><router-link to="/forgotPassword"> Forgot password</router-link></p>
          <p class="text-center mb-1 formLink"><router-link to="/register">Not registered yet? Create an account.</router-link></p>
        </v-card-text>
      </v-card>
    </v-row>
    </v-container>
    <v-snackbar
      max-width="80%"
      v-model="submitted"
      :timeout="4000"
      :top="true"
      color="error"
    >
     Invalid username or password
    </v-snackbar>
  </v-app>
</template>

<script>
import User from '../models/user';

export default {
  data() {
    return {
      showPassword: false,
      loading: false,
      user: new User('',''),
      submitted: false,
      isFormValid: false,
      emailRules: [
        v => !!v.trim() || 'Email is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
      ],
      passwordRules: [
        v => !!v || 'Password is required'
      ]
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    }
  },
  created() {
    if (this.loggedIn) {
      this.$router.push('/wishes');
    }
  },
  methods: {
    async handleLogin() {
        this.loading = true;
        try {
          await this.$store.dispatch('auth/login', this.user);
          this.$router.push('/wishes');
        }
        catch (err) {
          this.loading = false;
          this.submitted = true;
        }
    }
  }
}
</script>

<style scoped>
    .app {
      background-color:  #5E35B1
    }
    a {
      text-decoration: none;
      color: inherit !important;
    }
    .formLink {
      cursor: pointer;
    }
    .formLink:hover {
      color: #401786 !important;
    }
</style>