<template>
    <v-app>
        <navbar></navbar>
       <v-main class="app pt-0">
       <p class="mt-10 mb-12 deep-purple--text text--darken-4 text-center text-h4 text-sm-h3 font-weight-light">Your wishlist</p>
        <v-card elevation="3" width="600" class="my-5 mx-auto" v-for="wish in wishlist" :key="wish._id" >
            <v-card-title>
                <span class="deep-purple--text text--darken-2">{{ wish.title }}</span>
                <v-spacer></v-spacer>
                <v-icon @click="deleteWish(wish._id)" color="deep-purple accent-5">mdi-delete</v-icon>
            </v-card-title>
            <v-card-text>
                {{ wish.description }}
            </v-card-text> 
            <v-card-actions class="mt-0 pt-0">
                <v-btn
                    text
                    color="deep-purple accent-5"
                    :href="wish.link"
                    target="_blank"
                >
                    Buy / View
                </v-btn>
            </v-card-actions>
        </v-card>
       </v-main>
    </v-app>
</template>

<script>
import UserService from '@/services/user.service';
import Navbar from '@/components/Navbar';

export default {
    components: {
        Navbar
    },
    data() {
        return {
            wishlist: null,
        }
    },
    computed: {
        loggedIn() {
            return this.$store.state.auth.status.loggedIn;
        }
    },
    async created() {
        if (!this.loggedIn) {
            this.$router.push('/');
        }
        try {
            const data = await UserService.getWishlist();
            const { wishes } = data.data;
            this.wishlist = wishes;
        }
        catch (err) {
            throw new Error(err);
        }
    },
    methods: {
        async deleteWish(id) {
            try {
                const res = await UserService.deleteWish(id);
                if (res.success === true) {
                    this.wishlist = this.wishlist.filter(wish => wish._id !== id);
                }
            } catch (err) {
                throw new Error(err);
            }

        }
    }
}
</script>

<style scoped>
    .app {
        /* background-color: #280659; */
        background-color: lavender;
    }
    .toptitle {
        letter-spacing: 0.3rem;
    }
</style>