<template>
  <v-app>
    <v-container fluid fill-height class="app">
    <v-row align="center" justify="center">
      <v-card width="400" class="mx-auto"> 
        <v-card-title class="deep-purple--text text--darken-2 text-h5 text-sm-h4 font-weight-bold pl-8 mt-3">Forgot Password</v-card-title>
        <v-card-text>
          <v-form v-model="isFormValid">
          <v-text-field 
            v-model="email"
            class="pl-3 pr-5"
            label="Email" 
            :rules="emailRules"
            prepend-icon="mdi-email"
          />
          </v-form>
        </v-card-text>
        <v-card-actions>
        <v-btn 
            link
            to="/"
            color="deep-purple white--text" 
            width="30%"
            class="mx-auto ml-4 mr-4 mb-5">
            Back
          </v-btn>
          <v-spacer></v-spacer> 
          <v-btn 
            @click.prevent="handleForgotPassword"
            :disabled="loading || !isFormValid"
            width="30%"
            color="deep-purple white--text" 
            class="mx-auto ml-4 mr-4 mb-5">      
            Submit
          </v-btn>
        </v-card-actions>   
      </v-card>
    </v-row>
    </v-container>
    <v-snackbar
      v-model="submitted"
      :timeout="4000"
      :top="true"
      color="success"
    >
     A password reset link will been sent to email if it is valid.
    </v-snackbar>
  </v-app>
</template>

<script>
import UserService from '@/services/user.service';

export default {
  data() {
    return {
      email: '',
      isFormValid: false,
      loading: false,
      submitted: false,
      emailRules: [
        v => !!v.trim() || 'Email is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
      ]
    };
  },
  
  methods: {
    async handleForgotPassword() {
        this.loading = true;
        try {
            await UserService.sendPasswordEmail(this.email);
            this.submitted = true;
            this.loading = false;
        } catch (err) {
            this.submitted = true;
            this.loading = false;
        }
        
    }
  }
}
</script>

<style scoped>
  .app {
      background-color: #5E35B1;
    }
</style>