<template>
    <v-app>
        <navbar></navbar>
        <v-main class="app pt-0">
            <v-card width="400" class="mx-auto card"> 
                <v-card-title class="deep-purple--text text--darken-4 text-h4 font-weight-light pl-8 mt-3">Add new wish</v-card-title>
                <v-card-text>
                    <v-form ref="form" v-model="isFormValid">
                        <v-text-field 
                            v-model="wish.title"
                            class="pl-3 pr-5"
                            label="Title" 
                            :rules="titleRules"
                        />
                        <v-text-field 
                            v-model="wish.description"
                            class="pl-3 pr-5"
                            label="Description (Optional)" 
                        />
                        <v-text-field 
                            v-model="wish.link"
                            class="pl-3 pr-5"
                            label="Buy link (Optional)" 
                        />
                    </v-form>
                </v-card-text>
                <v-card-actions>
                    <v-btn 
                        :disabled="submitted || !isFormValid"
                        color="deep-purple accent-5 white--text" 
                        width="90%"
                        class="mx-auto mb-5"
                        @click.prevent="handleCreate"
                    >
                        Create
                    </v-btn>
                </v-card-actions>  
            </v-card>
            <v-snackbar
                v-model="showMessage"
                :timeout="3000"
                :top="true"
                :color="message.success ? 'success' : 'error'"
                >
                {{message.msg}}
            </v-snackbar>
        </v-main>
    </v-app>
</template>

<script>
import Wish from '@/models/wish';
import UserService from '@/services/user.service';
import Navbar from '@/components/Navbar';

export default {
    components: {
        Navbar
    },
    data() {
        return {
            wish: new Wish('', '', '', ''),
            submitted: false,
            isFormValid: false,
            showMessage: false,
            message: {},
            titleRules: [
                v => (v && !!v.trim()) || 'Title is required',
                v => (v && v.length >= 5) || 'Title must be atleast 5 characters long'
            ]
        }
    },
    computed: {
        loggedIn() {
            return this.$store.state.auth.status.loggedIn;
        }
    },
    created() {
        if (!this.loggedIn) {
            this.$router.push('/');
        }
    },
    methods: {
        async handleCreate() {
            this.submitted = true;
            try {
                await UserService.createWish(this.wish);
                this.$refs.form.reset();
                this.submitted = false;
                this.showMessage = true;
                this.message = {success: true, msg: 'Successfully added wish!'};
            } catch (err) {
                this.submitted = false;
                this.showMessage = true;
                this.message = {success: false, msg: 'Could not add wish due to server error.'};
            }
        }
    }
}
</script>

<style scoped>
    .app {
        background-color: lavender;
    }
    .card {
        margin-top: 10%;
    }
</style>