<template>
  <v-app>
    <v-container fluid fill-height class="app">
    <v-row align="center" justify="center">
      <v-card width="400" class="mx-auto"> 
        <v-card-title class="deep-purple--text text--darken-2 text-h5 text-sm-h4 pl-8 mt-3">Change Password</v-card-title>
        <v-card-text>
          <v-form v-model="isFormValid">
          <v-text-field 
            v-model="password1"
            class="pl-3 pr-5"
            label="Password" 
            :rules="password1Rules"
            type="password"
            prepend-icon="mdi-lock"
          />
          <v-text-field 
            v-model="password2"
            class="pl-3 pr-5"
            label="Confirm Password" 
            :rules="password2Rules"
            type="password"
            prepend-icon="mdi-lock"
          />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn 
            @click.prevent="handleChangePassword"
            :disabled="loading || !isFormValid || (password1 !== password2)"
            width="40%"
            color="deep-purple white--text" 
            class="mx-auto ml-4 mr-4 mb-5">      
            Submit
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn 
            to="/"
            width="40%"
            color="deep-purple white--text" 
            class="mx-auto ml-4 mr-4 mb-5">      
            Back to login
          </v-btn>
        </v-card-actions>   
      </v-card>
    </v-row>
    </v-container>
    <v-snackbar
      v-model="submitted"
      :timeout="4000"
      :top="true"
      :color="message.success ? 'success': 'error'"
    >
     {{ message.msg }}
    </v-snackbar>
  </v-app>
</template>

<script>
import UserService from '@/services/user.service';

export default {
  data() {
    return {
      password1: '',
      password2: '',
      isFormValid: false,
      loading: false,
      submitted: false,
      message: {},
      password1Rules: [
        v => (v && !!v.trim()) || 'Password is required',
        v => (v && v.length >= 8) || 'Password must be atleast 8 characters long'
      ],
      password2Rules: [
        v => (v && !!v.trim()) || 'This field is required',
        () => (this.password1 === this.password2) || 'Passwords do not match'
      ]
    };
  },
  
  methods: {
    async handleChangePassword() {
        this.loading = true;
        try {
            await UserService.changePassword(this.$route.params.id, this.password1);
            this.submitted = true;
            this.loading = false;
            this.message = {success: true, msg: 'Password changed successfully'};
        } catch (err) {
            this.submitted = true;
            this.loading = false;
            this.message = {success: false, msg: 'Could not change password due to server error'};
        }
        
    }
  }
}
</script>

<style scoped>
  .app {
      background-color:  #5E35B1;
    }
</style>