import axios from 'axios';
import authHeader from './auth-header';

const API_URL = process.env.NODE_ENV === 'production' 
    ? 'https://wishlist20.herokuapp.com/api/' 
    : 'http://localhost:3000/api/';

class UserService {
    async getSharedWishlist(id) {
        try {
            const data = await axios.get(API_URL + `sharedwishlist/${id}`);
            return data;
        }
        catch (err) {
            throw new Error(err.response.data.error);
        }
    }

    async getWishlist() {
        try {
            const data = await axios.get(API_URL + 'wishes', { headers: authHeader() });
            return data;
        }
        catch (err) {
            throw new Error(err.response.data.error);
        }
    }

    async createWish(wish) {
        try {
            const data = await axios.post(API_URL + 'wish',  
                { wish: wish }, 
                { headers: authHeader() }
            );
            return data.data;
        } 
        catch (err) {
            throw new Error(err.response.data.error);
        }
    }

    async deleteWish(id) {
        try {
            const data = await axios.delete(API_URL + 'wish', {
                headers: authHeader(),
                params: { wishid: id }
            });
            return data.data;
        }
        catch (err) {
            throw new Error(err.response.data.error);
        }
    }

    async sendPasswordEmail(email) {
        try {
            const res = await axios.post(API_URL + 'passwordEmail', { email });
            return res.data;
        }
        catch (err) {
            throw new Error(err.response.data.error);
        }
    }

    async changePassword(passresetlink, newpassword) {
        try {
            await axios.post(API_URL + 'changePassword', {passresetlink, newpassword});
        } catch (err) {
            throw new Error(err.response.data.error);
        }
    }

    async resetShareLink() {
        try {
            const res = await axios.post(API_URL + 'resetShareLink', {}, { headers: authHeader() });
            return res.data;
        } catch (err) {
            throw new Error(err.response.data.error);
        }
    }
}

export default new UserService();